import React, { Component } from "react";
import axios from 'axios';

import { Form, Button, Tabs, Tab } from "react-bootstrap";

export default class MyRegisLoginForm extends Component  {

    constructor(props){
        
        super(props)

        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangeSecretcode = this.onChangeSecretcode.bind(this);
        this.onChangeSecretcodeRegistration = this.onChangeSecretcodeRegistration.bind(this);
        this.onChangeSecretcodeConfirmation = this.onChangeSecretcodeConfirmation.bind(this);
        this.onSubmitLogin = this.onSubmitLogin.bind(this);
        this.onSubmitRegister = this.onSubmitRegister.bind(this);

        this.state = {
            username : '',
            secretcode: '',
            secretcodeRegistration: '',
            secretcodeConfirmation: '',
            checklist: [],
            notifLogin: 'Sedang mengontak server',
            notifRegister: ''
        }

        axios.post(process.env.REACT_APP_API_URL+'/api')
            .then(res => {
                this.setNotifFor5s("login", "Server sudah merespon. Silahkan login.")
            })
    }

    onChangeUsername(e){
        this.setState({
            username: e.target.value
        })
    }

    onChangeSecretcode(e){
        this.setState({
            secretcode: e.target.value
        })
    }

    onChangeSecretcodeRegistration(e){
        this.setState({
            secretcodeRegistration: e.target.value
        })
    }

    onChangeSecretcodeConfirmation(e){
        this.setState({
            secretcodeConfirmation: e.target.value
        })
    }

    onSubmitRegister(e){
        e.preventDefault();

        const user = {
            username: this.state.username,
            secretcode: this.state.secretcodeRegistration
        }

        if(this.state.secretcodeRegistration === this.state.secretcodeConfirmation){
            axios.post(process.env.REACT_APP_API_URL+'/api/users/register', user)
            .then(res => {
                this.setState({
                    notifRegister: "Registrasi berhasil! Silahkan login."
                })
            }).catch(err => {
                this.setNotifFor5s("register", err.response.data)
            });
        }else{
            this.setNotifFor5s("register", "Error: Password beda..")
        }
    }

    onSubmitLogin(e){
        e.preventDefault();

        this.setState({
            notifLogin: "mencoba masuk.."
        });

        const user = {
            username: this.state.username,
            secretcode: this.state.secretcode
        }

        axios.post(process.env.REACT_APP_API_URL+'/api/users/login', user)
            .then(res => {
                window.location = '/'+res.data._id;
            }).catch(err => {
                this.setNotifFor5s("login", err.response.data)
            });
    }

    setNotifFor5s(place, text){
        if(place === "login"){
            this.setState({
                notifLogin: text
            });
            setInterval(() => {
                this.setState({
                    notifLogin: null
                }); 
            }, 5000);
        }else if(place === "register"){
            this.setState({
                notifRegister: text
            });
            setInterval(() => {
                this.setState({
                    notifRegister: null
                }); 
            }, 5000);
        }
        
    }

    render () {
        return (
            <>
                <Tabs defaultActiveKey="login" id="uncontrolled-tab-example">
                    <Tab eventKey="login" title="Login">
                        <Form onSubmit={this.onSubmitLogin} className="mt-3">
                            <Form.Group controlId="formBasicEmail" className="mb-2">
                                <Form.Label>Alamat email</Form.Label>
                                <Form.Control type="email" placeholder="Tulis alamat Gmail" name="username" value={this.state.username} onChange={this.onChangeUsername} required/>
                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Tulis password" name="secretcode" value={this.state.secretcode} onChange={this.onChangeSecretcode} required/>
                            </Form.Group>

                            <Button variant="primary" type="submit">
                                Login
                            </Button>
                            <span className="ml-2">{this.state.notifLogin}</span>
                        </Form>
                    </Tab>
                    <Tab eventKey="register" title="Register">
                        <Form onSubmit={this.onSubmitRegister} className="mt-3">
                            <Form.Group controlId="formBasicEmailRegister" className="mb-1">
                                <Form.Label>Alamat email</Form.Label>
                                <Form.Control type="email" placeholder="Tulis alamat Gmail" name="username" value={this.state.username} onChange={this.onChangeUsername} required/>
                                <Form.Text className="text-muted">
                                    Tolong gunakan Gmail karena nanti akan pakai Google oAuth
                                </Form.Text>
                            </Form.Group>

                            <Form.Group controlId="formBasicPasswordRegister">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Tulis password" name="secretcode_registration" value={this.state.secretcodeRegistration} onChange={this.onChangeSecretcodeRegistration} required/>
                            </Form.Group>

                            <Form.Group controlId="formBasicPasswordRegisterConfirmation">
                                <Form.Label>Konfirmasi Password</Form.Label>
                                <Form.Control type="password" placeholder="Tulis ulang password" name="confirm_secretcode" value={this.state.secretcodeConfirmation} onChange={this.onChangeSecretcodeConfirmation} required/>
                            </Form.Group>

                            <Button variant="primary" type="submit">
                                Register
                            </Button>
                            <span className="ml-2">{this.state.notifRegister}</span>
                        </Form>
                    </Tab>
                </Tabs>
            </>
        )
    }
}