import React, { Component } from "react";
import axios from 'axios';

import { Button, Form, Table } from "react-bootstrap";


class AgileChecklist extends Component {

    constructor(props) {

        super(props);

        this.handleChange = this.handleChange.bind(this);
        this.simpanChecklist = this.simpanChecklist.bind(this);

        this.state = {
            practices: [],
            user: null,
            checklistWhy: [],
            notif: null
        };
    
    }

    componentDidMount() {
        axios.post(process.env.REACT_APP_API_URL+'/userid/'+this.props.match.params.id)
            .then(res => {
                console.log(res.data);

                var checklistWhy_temp = []

                res.data.practices.forEach( practice => {

                    var is_checked_temp
                    var the_why_temp = "(isi checklist untuk penjelasan)"

                    res.data.user.checklist.forEach( p => {
                        
                        if ( Object.keys(p)[0] === practice.slug ){
                            
                            if ( Object.values(p)[0] === true ){
                                is_checked_temp = true
                                the_why_temp = practice.if_yes_advice
                            }else{
                                is_checked_temp = false
                                the_why_temp = practice.if_no_advice
                            }
                        }
                        
                    })
                    
                    checklistWhy_temp.push({
                        id: practice._id,
                        slug: practice.slug,
                        title: practice.title,
                        description: practice.description,
                        if_no_advice: practice.if_no_advice,
                        if_yes_advice: practice.if_yes_advice,
                        is_checked: is_checked_temp,
                        the_why: the_why_temp
                    })
                })

                this.setState( {
                    practices: res.data.practices,
                    user: res.data.user,
                    checklistWhy: checklistWhy_temp
                } )

            })
            .catch((error) => {
                console.log(error);
            })
    }

    checklistMapping() {
        return this.state.checklistWhy.map( (item, index) => {
            const font085 = {fontSize: '0.85em'}
            return  <tr key={item.id}>
                        <td>
                            <Form.Check 
                                type='checkbox'
                            >
                                <Form.Check.Input 
                                    type="checkbox"
                                    value={item.id}
                                    id={item.slug}
                                    defaultChecked={item.is_checked ? "checked" : false}
                                    onChange={this.handleChange} />
                                <Form.Check.Label>{index+1}. {item.title}</Form.Check.Label>
                                <div className="text-muted" 
                                    dangerouslySetInnerHTML={{__html: item.description}}></div>
                            </Form.Check>
                        </td>
                        <td style={font085}>
                            <span dangerouslySetInnerHTML={{__html: item.the_why}}></span>
                        </td>
                    </tr>
                    ;
          })
    }

    render(){
        const width50 = {width: '50%'}
        return (
            <>
                <h3>Checklist Ketangkasan (berdasarkan pelbagai bingkai kerja populer)</h3>
                <Table bordered hover>
                    <thead>
                        <tr>
                            <th style={width50}>The Checklist</th>
                            <th style={width50}>The Why</th>
                        </tr>
                    </thead>
                    <tbody>
                        { this.checklistMapping() }
                    </tbody>
                </Table>
                <Button
                    variant="primary"
                    onClick={this.simpanChecklist}
                    >
                    Simpan
                </Button>
                <span>{this.state.notif}</span>
                <hr/>
                <h4>Kamipun mendengar penguna! Kirim pujian, kritik, atau saran ke Telegram <a href='https://t.me/RizkySyaiful'>@RizkySyaiful</a></h4>
            </>
        )
    }


    simpanChecklist = event => {
        this.setState({
            notif: " menyimpan.."
        });

        var user_checklist = []
        
        this.state.checklistWhy.forEach( item => {
            if (typeof item.is_checked !== 'undefined') {
                user_checklist.push({[item.slug]: item.is_checked})
            }            
        })

        axios.post(process.env.REACT_APP_API_URL+'/api/users/checklist-update', {
            user: this.state.user,
            checklist: user_checklist 
        })
            .then(res => {
                console.log(res)
                this.setState({
                    notif: " berhasil tersimpan"
                });
                setInterval(() => {
                    this.setState({
                        notif: null
                    });
                }, 2000);
            });

    }

    handleChange = event => {
        
        const { value, checked } = event.target
        console.log([value] + " " + checked)

        let items = [...this.state.checklistWhy]
        let item = items.filter( item => {
            return item.id === value
        })[0]
         
        item.is_checked = checked
        item.the_why = checked ? item.if_yes_advice : item.if_no_advice

        let temp = this.state.checklistWhy
        for (var i = 0; i < temp.length; i++) {
            if (temp[i].id === value){
                temp[i] = item
               
                this.setState({
                    checklistWhy: temp
                })
            }
        }
    
    }
}

export default AgileChecklist